import '../css/main.css';

import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';

import { WhatsAppService } from '../services/whatsapp-service';
import { ReducerStore } from '../app/store';

import { Breadcumb } from '../components/Breadcumb';
import { TitlePage } from '../components/TitlePage';
import { validateToken } from '../reducers/authenticatedSlice';

export function Home() {
  const auth = useSelector((state: ReducerStore) => state.authenticated);
  const dispatch = useDispatch();
  let navigate = useNavigate();

  useEffect(() => {
    dispatch(validateToken(auth.token));
    if (!auth.token) {
      navigate(auth.redirectLoginPageUri, { replace: true });
    }
    const navbar = document.querySelector('#navbarresponsive') as HTMLElement;
    if (navbar) navbar.style.display = 'flex';
  }, [auth, navigate, dispatch]);

  return (
    <div className="container-main">
      <Breadcumb page={[]} />

      <TitlePage title={`Ol, ${auth ? auth.userName : 'Seja Bem Vindo'}!`} />
      <p>
        Este  o <strong>ARV - Controll &copy;</strong>, um Sistema de
        Gerenciamento para sua empresa.
      </p>
      <p>
        O sistema  focado na simplicidade e acessibilidade, podendo ser
        utilizado pelo computador ou pelo celular, sem perda de desempenho ou
        produtividade.
      </p>
      <div className="container mt-5 mb-5">
        <h5 className="title-page">Ajuda</h5>
        <p>
          Caso precise de ajuda ou queira tirar dvidas sobre o sistema, entre
          em contato com o desenvolvedor.
        </p>
        <button
          type="button"
          onClick={(e) => WhatsAppService.redirectToWhatsapp(e, '35999554534')}
          className="btn btn-success col"
        >
          Entrar em contato
        </button>
      </div>
      <p className="mt-5 mb-3 text-muted text-center">
        ARV - Controll &copy; 2022
      </p>
    </div>
  );
}
