import { createSlice } from '@reduxjs/toolkit';
import { ReducerActionType } from './types/reducerType';

export type ISales = {
  id: string;
  client: string;
  procedure: string;
  date: string;
  price: string;
};

const initialState: ISales = {
  id: '',
  client: '',
  procedure: '',
  date: '',
  price: '',
};

const salesSlice = createSlice({
  name: 'sales',
  initialState,
  reducers: {
    onCreateSales: {
      reducer(state: ISales, action: ReducerActionType) {
        state.id = action.payload.id;
        state.client = action.payload.client;
        state.procedure = action.payload.procedure;
        state.date = action.payload.date;
        state.price = action.payload.price;
      },
      prepare(payload) {
        return {
          payload,
        };
      },
    },
  },
});

export default salesSlice.reducer;
export const { onCreateSales } = salesSlice.actions;
