import { WhatsAppService } from '../services/whatsapp-service';

import moment from 'moment';
import { Paper } from '@material-ui/core';
import { randomId } from '../utils/random';
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { onEditClient } from '../reducers/scheduleSlice';
import { onCreateSales } from '../reducers/salesSlice';
import { CreateSalesBySchedule } from '../pages/CreateSalesBySchedule';

type InputProps = {
  item: any;
  setId: any;
  setIdScheduleDeleted: any;
  expired: boolean;
};

export function ScheduleCard(props: InputProps) {
  const { item, setId, setIdScheduleDeleted, expired } = props;

  const navigate = useNavigate();
  const dispatch = useDispatch();

  return (
    <Paper elevation={3} key={randomId()}>
      <CreateSalesBySchedule
        id="create-sales-by-schedule"
        data_target="#create-sales-by-schedule"
      />

      <div className="mb-2 mt-3">
        <div className="card p-2 card-schedule">
          {expired ? (
            <div className="card-header border-bottom pb-2 mb-2">
              <div className="form-row">
                <h6 className="col text-danger font-weight-bold mb-2">
                  Horrio Expirado!
                </h6>
              </div>
            </div>
          ) : null}
          <div className="card-header border-bottom pb-2 mb-3">
            <div className="form-row" style={{ alignItems: 'center' }}>
              <h6 className="text-primary col font-weight-bold">
                Cliente:{' '}
                <small className="text-muted h6 mb-3">{item.client}</small>
              </h6>

              <div
                style={{
                  display: 'flex',
                }}
              >
                {item.phone && !item.isDefeated && (
                  <button
                    type="button"
                    className="col m-0 pl-2 pr-2 btn"
                    onClick={(e: React.BaseSyntheticEvent) =>
                      WhatsAppService.redirectToWhatsappWithMessage(
                        e,
                        item.client,
                        item.phone,
                        item.date,
                        item.time,
                        item.qtdTotalAtendimento,
                        item.qtdAtendimento,
                        item.procedure,
                      )
                    }
                    style={{
                      border: 'none',
                      backgroundColor: 'transparent',
                      outline: 'none',
                    }}
                  >
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="30"
                      height="30"
                      fill="green"
                      className="bi bi-whatsapp"
                      viewBox="0 0 16 16"
                    >
                      <path d="M13.601 2.326A7.854 7.854 0 0 0 7.994 0C3.627 0 .068 3.558.064 7.926c0 1.399.366 2.76 1.057 3.965L0 16l4.204-1.102a7.933 7.933 0 0 0 3.79.965h.004c4.368 0 7.926-3.558 7.93-7.93A7.898 7.898 0 0 0 13.6 2.326zM7.994 14.521a6.573 6.573 0 0 1-3.356-.92l-.24-.144-2.494.654.666-2.433-.156-.251a6.56 6.56 0 0 1-1.007-3.505c0-3.626 2.957-6.584 6.591-6.584a6.56 6.56 0 0 1 4.66 1.931 6.557 6.557 0 0 1 1.928 4.66c-.004 3.639-2.961 6.592-6.592 6.592zm3.615-4.934c-.197-.099-1.17-.578-1.353-.646-.182-.065-.315-.099-.445.099-.133.197-.513.646-.627.775-.114.133-.232.148-.43.05-.197-.1-.836-.308-1.592-.985-.59-.525-.985-1.175-1.103-1.372-.114-.198-.011-.304.088-.403.087-.088.197-.232.296-.346.1-.114.133-.198.198-.33.065-.134.034-.248-.015-.347-.05-.099-.445-1.076-.612-1.47-.16-.389-.323-.335-.445-.34-.114-.007-.247-.007-.38-.007a.729.729 0 0 0-.529.247c-.182.198-.691.677-.691 1.654 0 .977.71 1.916.81 2.049.098.133 1.394 2.132 3.383 2.992.47.205.84.326 1.129.418.475.152.904.129 1.246.08.38-.058 1.171-.48 1.338-.943.164-.464.164-.86.114-.943-.049-.084-.182-.133-.38-.232z" />
                    </svg>
                  </button>
                )}
                <button
                  type="button"
                  className="col btn border-0 m-0 p-0 pl-2 pr-2"
                  onClick={(e) => {
                    dispatch(
                      onEditClient({
                        id: item.id,
                        client: item.client,
                        procedure: item.procedure,
                        date: item.date,
                        time: item.time,
                        price: item.price,
                        phone: item.phone,
                        pacote: item.pacote,
                        qtdTotalAtendimento: item.qtdTotalAtendimento,
                        qtdAtendimento: item.qtdAtendimento,
                      }),
                    );
                    navigate(`/edit-schedule/${item.id}`);
                  }}
                  style={{
                    border: 'none',
                    backgroundColor: 'transparent',
                    outline: 'none',
                  }}
                >
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="30"
                    height="30"
                    fill="blue"
                    className="bi bi-pencil-square"
                    viewBox="0 0 16 16"
                  >
                    <path d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z" />
                    <path
                      fillRule="evenodd"
                      d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z"
                    />
                  </svg>
                </button>

                <button
                  type="button"
                  className="col btn border-0 m-0 pl-2 pr-2 btn btn-outline-danger"
                  data-toggle="modal"
                  data-target="#delete-client-schedule"
                  onClick={(e) => {
                    setIdScheduleDeleted(item.id);
                  }}
                  style={{
                    border: 'none',
                    backgroundColor: 'transparent',
                    outline: 'none',
                  }}
                >
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="30"
                    height="30"
                    fill="red"
                    className="bi bi-trash-fill"
                    viewBox="0 0 16 16"
                  >
                    <path d="M2.5 1a1 1 0 0 0-1 1v1a1 1 0 0 0 1 1H3v9a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2V4h.5a1 1 0 0 0 1-1V2a1 1 0 0 0-1-1H10a1 1 0 0 0-1-1H7a1 1 0 0 0-1 1H2.5zm3 4a.5.5 0 0 1 .5.5v7a.5.5 0 0 1-1 0v-7a.5.5 0 0 1 .5-.5zM8 5a.5.5 0 0 1 .5.5v7a.5.5 0 0 1-1 0v-7A.5.5 0 0 1 8 5zm3 .5v7a.5.5 0 0 1-1 0v-7a.5.5 0 0 1 1 0z" />
                  </svg>
                </button>
              </div>
            </div>
          </div>
          <div className="list-group-item pb-2">
            {item.pacote && (
              <h6 className="card-subtitle text-danger font-weight-bold border-bottom pb-3 pt-2 mb-3">
                Pacote
              </h6>
            )}
            <h6 className="card-subtitle text-primary font-weight-bold border-bottom pb-3 pt-2 mb-3">
              Descrio:{' '}
              <small className="text-muted h6 mb-3">{item.procedure}</small>
            </h6>
            <div className="form-row pt-2 pb-2 mb-2">
              <h6 className="card-subtitle col text-primary font-weight-bold">
                Horrio:{' '}
                <small className="text-muted h6 mb-2">{item.time}hrs</small>
              </h6>
              <h6 className="card-subtitle col text-primary font-weight-bold">
                Data:{' '}
                <small className="text-muted h6 mb-3">
                  {moment(item.date).format('DD/MM/YYYY')}
                </small>
              </h6>
            </div>
            {item.pacote && (
              <div className="form-row border-top">
                <h6 className="col card-subtitle text-primary font-weight-bold pb-2 pt-2 mt-2">
                  Total atendimentos:{' '}
                  <small className="text-muted h6 mb-2">
                    {item.qtdTotalAtendimento}
                  </small>
                </h6>
                <h6 className="col card-subtitle text-primary font-weight-bold pb-2 pt-2 mt-2">
                  Atual:{' '}
                  <small className="text-muted h6 mb-2">
                    {item.qtdAtendimento}
                  </small>
                </h6>
              </div>
            )}
          </div>
          <div className="mt-3">
            <div className="form-row">
              <button
                type="button"
                className="col m-1 btn btn-outline-primary"
                data-toggle="modal"
                data-target="#finalyt-schedule"
                onClick={(e) => setId(item.id)}
              >
                Finalizar
              </button>
              <button
                type="button"
                className="col m-1 btn btn-outline-primary"
                data-toggle="modal"
                data-target="#create-sales-by-schedule"
                onClick={(e) => {
                  dispatch(
                    onCreateSales({
                      id: item.id,
                      client: item.client,
                      procedure: item.procedure,
                      date: item.date,
                    }),
                  );
                }}
              >
                Gerar Venda
              </button>
            </div>
          </div>
        </div>
      </div>
    </Paper>
  );
}
