import { IClientHistory } from '../api/types/ClientHistory';
import { randomId } from '../utils/random';

import * as React from 'react';
import Box from '@mui/material/Box';
import Collapse from '@mui/material/Collapse';
import IconButton from '@mui/material/IconButton';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';

type InputProps = {
  clientHistory: IClientHistory[];
};

export function TableHistory(props: InputProps) {
  const { clientHistory } = props;

  return (
    <TableContainer component={Paper} className="mt-4">
      <Table aria-label="collapsible table">
        <TableHead
          style={{
            backgroundColor: '#4083be',
          }}
        >
          <TableRow>
            <TableCell />
            <TableCell
              style={{
                color: 'white',
              }}
            >
              Cliente
            </TableCell>
            <TableCell
              align="center"
              style={{
                color: 'white',
              }}
            >
              Data
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {clientHistory.map((row) => (
            <Row
              key={randomId()}
              row={createData(row.client, row.date, row.description)}
            />
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}

function createData(client: string, date: string, description: string) {
  return {
    client,
    date,
    description,
  };
}

function Row(props: { row: ReturnType<typeof createData> }) {
  const { row } = props;
  const [open, setOpen] = React.useState(false);

  return (
    <React.Fragment>
      <TableRow sx={{ '& > *': { borderBottom: 'unset' } }}>
        <TableCell>
          <IconButton
            aria-label="expand row"
            size="small"
            onClick={() => setOpen(!open)}
            style={{ outline: 'none' }}
          >
            {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
          </IconButton>
        </TableCell>
        <TableCell component="th" scope="row">
          {row.client}
        </TableCell>
        <TableCell align="center">
          {new Date(row.date).toLocaleDateString('pt-BR', {
            timeZone: 'UTC',
          })}
        </TableCell>
      </TableRow>
      <TableRow>
        <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6}>
          <Collapse in={open} timeout="auto" unmountOnExit>
            <Box sx={{ margin: 1 }}>
              <Typography variant="inherit" gutterBottom component="div">
                <strong>Descrio</strong>
              </Typography>
              <Typography variant="body2" gutterBottom component="div">
                {row.description}
              </Typography>
            </Box>
          </Collapse>
        </TableCell>
      </TableRow>
    </React.Fragment>
  );
}
