import { randomId } from '../utils/random';
import { ISales } from '../api/types/Sales';

import * as React from 'react';
import Box from '@mui/material/Box';
import Collapse from '@mui/material/Collapse';
import IconButton from '@mui/material/IconButton';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';

type InputProps = {
  sales: ISales[];
};

export function TableSales(props: InputProps) {
  const { sales } = props;

  return (
    <TableContainer component={Paper} className="mt-4 mb-4">
      <Table aria-label="collapsible table">
        <TableHead
          style={{
            backgroundColor: '#4083be',
          }}
        >
          <TableRow>
            <TableCell />
            <TableCell
              style={{
                color: 'white',
              }}
            >
              Cliente
            </TableCell>
            <TableCell
              align="center"
              style={{
                color: 'white',
              }}
            >
              Data
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {sales.map((row) => (
            <Row
              key={randomId()}
              row={createData(row.client, row.date, row.description, row.price)}
            />
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}

function createData(
  client: string,
  date: string,
  description: string,
  price: string,
) {
  return {
    client,
    date,
    description,
    info: [
      {
        price,
      },
    ],
  };
}

function Row(props: { row: ReturnType<typeof createData> }) {
  const { row } = props;
  const [open, setOpen] = React.useState(false);

  return (
    <React.Fragment>
      <TableRow sx={{ '& > *': { borderBottom: 'unset' } }}>
        <TableCell>
          <IconButton
            aria-label="expand row"
            size="small"
            onClick={() => setOpen(!open)}
            style={{ outline: 'none' }}
          >
            {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
          </IconButton>
        </TableCell>
        <TableCell component="th" scope="row">
          {row.client}
        </TableCell>
        <TableCell align="center">
          {new Date(row.date).toLocaleDateString('pt-BR', {
            timeZone: 'UTC',
          })}
        </TableCell>
      </TableRow>
      <TableRow>
        <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6}>
          <Collapse in={open} timeout="auto" unmountOnExit>
            <Box sx={{ margin: 1 }}>
              <Typography variant="inherit" gutterBottom component="div">
                <strong>Descrio</strong>
              </Typography>
              <Typography variant="body2" gutterBottom component="div">
                {row.description}
              </Typography>

              <Table size="small" aria-label="purchases">
                <TableHead>
                  <TableRow>
                    <TableCell align="right" variant="body">
                      <strong
                        style={{
                          color: '#4083be',
                        }}
                      >
                        Total
                      </strong>
                    </TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {row.info.map((info) => (
                    <TableRow key={randomId()}>
                      <TableCell component="th" scope="row" align="right">
                        {info.price}
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </Box>
          </Collapse>
        </TableCell>
      </TableRow>
    </React.Fragment>
  );
}
